import axios from "axios";
const http = axios.create({
  baseURL: import.meta.env.VITE_BASE_API,
  timeout: 10 * 1000
});

http.interceptors.request.use(
  config => {
    console.log(1234);
    // throw new Error('')
    return config;
  },
  err => {
    console.log(err);
    //   
    console.log("");
    return Promise.reject(err);
  }
);

http.interceptors.response.use(
  res => {
    if (res.status === 200) {
      //  status200 
      return res.data;
    }
  },
  err => {
    //  Promise.reject(err) promise  return  catch  then
    console.log(err);
    // throw new Error  
    return Promise.reject(err);
  }
);

export default http;
