/**
 * function
 * @name useHttp
 * url 
 * options 
 * requestCallback 
 * responseCallback 
 * failCallback 
 */
import { ref } from "vue"

const baseURL = "http://192.168.100.234:49000"
export function useHttp(
  url,
  options = {},
  requestCallback,
  responseCallback,
  failCallback
) {
  if (typeof url !== "string" && url instanceof Request && url instanceof URL) {
    console.warn("url")
  }
  if (options.headers && typeof options !== "object") {
    console.warn("options")
  }
  const result = ref(null)
  const fail = ref(null)

  function onRequest() {
    if (!options.headers?.["Content-Type"]) {
      options.headers = Object.assign({}, options.headers)
      options.headers["Content-Type"] = "application/json"
      options.body = JSON.stringify(options.body)
    }
    // 
    requestCallback && requestCallback()
  }
  onRequest()
  fetch((baseURL || options.baseURL) + url, options)
    .then((res) => {
      console.log(res)
      if (res.ok) {
        if (options.responseType === "blob") return res.blob()
        else if (options.responseType === "text") return res.text()
        else if (options.responseType === "arrayBuffer")
          return res.arrayBuffer()
        return res.json()
      } else {
        throw new Error("")
      }
    })
    .then((res) => {
      // 
      responseCallback && responseCallback(res)
      result.value = res
    })
    .catch((err) => {
      // console.log(err)
      // 
      failCallback && failCallback(err)
      fail.value = err
    })

  return {
    result,
    fail
  }
}
